.stages{

    &__header{
        display: flex;
        flex-direction: column;
        max-width: 463px;
        text-align: center;
        align-items: center;
        margin: 154px auto 0;
    }

    &__steps{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        margin-top: 200px;
    }
    &__steps-content{
        max-width: 300px;
        height: 300px;
        background: #FFFFFF;
        box-shadow: 0px 8px 15px rgba(158, 158, 158, 0.25);
        padding: 8px 16px;
    }
    &__steps-img{

        &_next{
            width: 75px;
            height: 97px;
        }
    }
}